html {
  box-sizing: border-box;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
}

a,
a:hover,
a:active,
a:focus {
  outline: 0;
  text-decoration: none;
}

:root {
  --color-bg-dark: #000000;
  --color-bg-regular: #000000;
  --color-bg-light: #ffffff18;
  --color-text-light: #FFFFFF;
  --color-text-dark: #ffffffe7;
  --color-border-light: #FFFFFF;
  --color-green: #07e396;
  --color-red: #db0d2c;
  --color-yellow: #c6db0d;
  --color-rating: #ebff12;
  

  --color-grey-regular: #8fa6bc;
  --color-grey-light: #dadadb;
  --color-grey-dark: #375066;
  --color-blue: #6691ef;
  --color-blue-rgb: 102, 145, 239;
  --color-teal: #3adce3;
  
  --color-green-rgb: 72, 207, 143;
  --color-orange: #fab852;
  
  --color-red-rgb: 250, 128, 128;
  --color-purple: #8a60d9;
  --color-purple-rgb: 138, 96, 217;

  --font-size-xs: 0.625rem;
  --font-size-s: 0.75rem;
  --font-size-r: 0.875rem;
  --font-size-m: 1rem;
  --font-size-l: 1.125rem;
  --font-size-xxl: 1.25rem;

  --font-size-h3: 1.5rem;
  --font-size-h2: 1.875rem;
  --font-size-h1: 2rem;

  --border-radius-s: 3pt;
  --border-radius-m: 6pt;
  --border-radius-l: 8pt;

  --margin-xs: 3pt;
  --margin-s: 5pt;
  --margin-r: 10pt;
  --margin-m: 15pt;
  --margin-l: 20pt;
  --margin-xl: 25pt;
  --margin-xxl: 50pt;
  --margin-g: 100pt;

  --navbar-height: 50pt;

  --device-mobile: 540px;
  --device-tablet: 680px;
  --device-laptop: 960px;
  --device-desktop: 1440px;
  --device-widescreen: 2560px;
}
